.white {
  width: 265px;
  height: 265px;
  border-radius: 50%;
  border: 5px solid white;
  position: absolute;
}
.border_red {
  width: 275px;
  height: 275px;
  border-radius: 50%;
  border: 5px solid #eb544d;
  color: #eb544d;
  position: relative;
  overflow: hidden;
}
.border_red_wave {
    position: absolute;
    width: 500px;
    height: 500px;
    left: 50%;
    border-radius: 45%;
    z-index: -5;
    background-color: #eb544d;
    -webkit-animation-name: rotate;
            animation-name: rotate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-duration: 9s;
            animation-duration: 9s;
    opacity: 0.5;
}
.border_red_wave_after {
      position: absolute;
      width: 500px;
      height: 500px;
      border-radius: 45%;
      left: 50%;
      background-color: #eb544d;
      z-index: -10;
